            this.afterTomorrowSamples=data.afterTomorrowSamples;
            this.valuePredict=data.valuePredict;
            this.inRankList=data.inRankList;
            this.marketRankList=data.marketRankList;
            this.inRankInfo = data.inRankInfo;
            this.marketRankInfo= data.marketRankInfo;
            this.interStock=data.interStock;
            this.HSStock=data.HSStock;
            this.interInfo=data.interInfo;
            this.marketInfo=data.marketInfo;
            let barOption = this.barOption;
            barOption.series[0].data=this.valuePredict;
            this.$set(this,'barOption',barOption);
            notify("success", "数据更新成功");
          }else {
            this.$set(this,'barOption',this.noDataKOption)
