<script>
export default {
  name: "LineRank",
  props: {
    /*
    行业最高,最低,平均,自身分数,排名,百分比位置
     */
    info: {
      highest: 0,
      lowest: 0,
      average: 0,
      self_score: 0,
      self_rank: "第一名",
      proportion: 0,
    },
    name:"",
  },
  data() {
    return {
      line_width: 0,
      position: 0,
      show: true,
    };
  },
  methods:{
    getPosition(proportion){
      if(proportion>=30) {return proportion-30+"%"}
      else {
        return proportion-5+"%"
      }
    }

  },
  mounted() {
    this.line_width = this.$refs.line.offsetWidth;
    this.position = this.line_width * this.info.proportion - 10 / 2;
  },
};
</script>

